@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Lato&display=swap');

/* base styles */

* {
  margin: 0;
  /*font-family: "Quicksand";*/
  font-family: 'Lato', sans-serif;
  background-color: black;
}

.navbar {
  padding: 20px;
  display: flex;
  align-items: center;
  max-width: 600px;
  margin: 0 auto;
  border-bottom: 1px solid #ffffff;
}

.navbar .logo {
  color: #1eff00;
  text-decoration: none;
  font-size: 25px;
  font-weight: 900;
}

.navbar .links {
  margin-left: auto;
}

.navbar a {
  margin-left: 16px;
  text-decoration: none;
  padding: 6px;
  color: white;
}

.navbar a:hover {
  color: white;
}

.content {
  max-width: 600px;
  margin: 40px auto;
  padding: 20px;
}

/* blog previews list */

.blog-preview {
  padding: 10px 16px;
  margin: 20px 0;
  text-decoration: none;
}

.blog-preview:hover {
  cursor: pointer;
}

.blog-preview h2 {
  font-size: 30px;
  color: #1eff00;
  margin-bottom: 8px;
}

.blog-preview h4 {
  font-size: 15px;
  margin-bottom: 8px;
  color: white;
}

.blog-page .blog .blog-title{
  font-size: 40px;
  color: #1eff00;
  margin-bottom: 8px;
}

.blog-page .blog .blog-author{
  font-size: 15px;
  margin-bottom: 13px;
  color: white;
}

.blog-page .blog .blog-body{
  text-align: justify;
  font-size: 25px;
  color: white;
}

.blog-page .blog .blog-delete{
  background: #1eff00;
  color: black;
  border: 0;
  font-size: 20px;
  padding: 8px;
  border-radius: 8px;
  cursor: pointer;
  font-weight: 700;
  margin-top: 10px;
}

.error-msg{
  color: white;
  font-size: 20px;
}

.status{
  color: white;
  font-size: 20px;
}

.create-null-msg{
  color: white;
}

/* create new blog form */

.create {
  max-width: 700px;
  margin: 0 auto;
  text-align: center;
}

.create label {
  text-align: left;
  display: block;
}

.create h2 {
  font-size: 20px;
  color: #1eff00;
  margin-bottom: 30px;
}

.create textarea{
  resize: none;
}

.create input, .create textarea, .create select {
  width: 100%;
  padding: 20px 10px;
  margin: 10px 0;
  border: 3px solid #1eff00;
  box-sizing: border-box;
  display: block;
  color: white;
  border-radius: 10px;
  font-size: 20px;
}

.create select:hover{
  cursor: pointer;
}

.create select option{
  background: black;
  color: #1eff00;
  font-size: 20px;
}

.create input, .create textarea, .create select:focus{
  outline: none;
}

.create button {
  background: #1eff00;
  color: black;
  border: 0;
  font-size: 20px;
  padding: 8px;
  border-radius: 8px;
  cursor: pointer;
  font-weight: 700;
}

/* 404 page */

.not-found{
  text-align: center;
}

.not-found .not-found-head{
  color: #1eff00;
  font-size: 50px;
}

.not-found .not-found-msg{
  color: #1eff00;
  text-decoration: none;
  font-size: 20px;
  font-weight: 600;
}